<<template>
    <div id="wizyty">
           
        
        <ul class="collection with-header">
           <li class="collection-header"><h4>Wizyty</h4></li>
            <li v-for="kli in klient" v-bind:key="kli.id" class="collection-item">
                <div class="chip orange white-text">{{kli.data_wizyty}} | {{kli.klient_id}} - {{kli.czas_uslugi}} </div>
                {{kli.imie_nazwisko}}   
                <router-link class="secondary-content" v-bind:to="{name:'widok-klient', params:{id_k: kli.id_k}}">
                 <i class="fa fa-eye"></i>
                </router-link>         
             </li>
        </ul>
      <div class="fixed-action-btn">
      <router-link to="/nowy" class="btn-floating btn-large blue">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
    </div>
</template>

<script>
import db from './firebaseInit'
import { functions } from 'firebase';
export default {
    name: 'wizyty',
    data (){
        return{
           klient: [],
        
        }
    },
    created () {
        db.collection('klient').orderBy('data_wizyty').get().then
        (querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'klient_id': doc.data().klient_id,
                    'imie_nazwisko': doc.data().imie_nazwisko,
                    'data_wizyty': doc.data().data_wizyty,
                    'usluga': doc.data().usluga,
                    'czas_uslugi': doc.data().czas_uslugi,
                    'id_k': doc.data().id_k
                }
                this.klient.push(data)
            })
        })
    },
    
}
</script>


