<template>
  <nav class="nav-extended">
    <div class="nav-wrapper black">
      <div class="container">
      <a class="brand-logo left">Aplikacja</a>
      <ul id="nav-mobile" class="right">
        <li v-if="isLoggedIn"><span class="email white-text">{{currentUser}}</span></li>
        <li v-if="!isLoggedIn"><router-link to="/logowanie">Logowanie</router-link></li>
        <li v-if="!isLoggedIn"><router-link to="/rejestracja">Rejestracja</router-link></li>
        <li v-if="isLoggedIn"><button v-on:click="logout" class="btn orange">Wyloguj</button></li>
      </ul>
      </div>
    </div>
    <div v-if="isLoggedIn" class="nav-content blue">
      <div class="container">
      <ul class="tabs tabs-transparent">
        <li class="tab"><router-link to="/">Dashboard</router-link></li>
        <li class="tab"><router-link to="/wizyty">Wizyty</router-link></li>
      </ul>
    </div>
    </div>
  </nav>

</template>

<script>
import firebase from 'firebase';
export default {
  name: 'navbar',
  data() {
    return {
      isLoggedIn: false,
      currentUser: false
    }
  },
  created() {
        if (firebase.auth().currentUser) {
      this.isLoggedIn = true;
      this.currentUser = firebase.auth().currentUser.email;
    }
  },
  methods: {
    logout: function() {
      firebase.auth().signOut().then(() => {
       this.$router.go({path: this.$router.path});
      });
    }
  }
}
</script>




