<<template>
    <div id="edycja-klient">
<h3>edytuj klienta</h3>
        <div class="row">
            <form @submit.prevent="updateKli" class="col s12">

                <div class="row">
                   <div class="input-field col s12"> 
                     <input type="text" v-model="imie_nazwisko" required>

                   </div>
                </div>
                <div class="row">
                   <div class="input-field col s12"> 
                     <input type="date" v-model="data_wizyty" required>
                     </div>
                </div>
                                <div class="row">
                   <div class="input-field col s12"> 
                     <input type="time" v-model="klient_id" required>
                   </div>
                </div>
                <div class="row">
                   <div class="input-field col s12"> 
                     <input type="time" v-model="czas_uslugi" required>
  
                   </div>
                </div>
                
                <div class="row">
                   <div class="input-field col s12"> 
                     <input type="text" v-model="usluga" required>

                   </div>
                </div>

                <button type="submit" class="btn">Zmień</button>
                <router-link to="/wizyty" class="btn grey">Anuluj</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'edycja-klient',
    data (){
        return{
           klient_id: null,
           imie_nazwisko: null,
           data_wizyty: null,
           usluga: null,
           czas_uslugi: null,
           id_k: null
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('klient').where('id_k','==', to.params.id_k).get().then(
            querySnapshot => {
            querySnapshot.forEach(doc =>{
                next(vm => {
                    vm.klient_id = doc.data().klient_id
                    vm.imie_nazwisko = doc.data().imie_nazwisko
                    vm.data_wizyty = doc.data().data_wizyty
                    vm.usluga = doc.data().usluga
                    vm.czas_uslugi = doc.data().czas_uslugi
                    vm.id_k = doc.data().id_k
                })
             })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('klient').where('id_k','==',this.$route.params.id_k).get().then
            (querySnapshot => {
                querySnapshot.forEach(doc =>{
                    this.klient_id = doc.data().klient_id
                    this.imie_nazwisko = doc.data().imie_nazwisko
                    this.data_wizyty=doc.data().data_wizyty
                    this.usluga= doc.data().usluga
                    this.czas_uslugi= doc.data().czas_uslugi
                    this.id_k= doc.data().id_k
                })
            })
        },
        updateKli (){
             db.collection('klient').where('id_k','==',this.$route.params.id_k).get().then
            (querySnapshot => {
                querySnapshot.forEach(doc =>{
                    doc.ref.update({
                        klient_id: this.klient_id,
                        imie_nazwisko: this.imie_nazwisko,
                        data_wizyty: this.data_wizyty,
                        usluga: this.usluga,
                        czas_uslugi: this.czas_uslugi,
                        id_k: this.id_k
                    })
                    .then(() =>{
                        this.$router.push({name: 'widok-klient', parms: {id_k: this.id_k}})
                    })  
                })
            })
        }
   
    }
}
</script>


