<<template>
   <div id="dashboard">
  <div class="container-fluid">
    <div class="row">
      <div class="col s12 m4">
        <div class="card blue white-text">  
          <div class="card-content valign-wrapper">
            <div class="card-text">
              <h1>Ilość klientów na dziś</h1>  
            </div>
        <i class="container large material-icons">people</i>
            </div>
          <div class="card-action" >
            {{licz}}
          </div>
        </div>
      </div>
      <div class="col s12 m4">
        <div class="card orange white-text">  
          <div class="card-content valign-wrapper">
            <div class="card-text">
            <h1>Ilość klientów na jutro</h1>
            </div>
            <i class="container large material-icons">people</i>
          </div>
          <div class="card-action">
            {{licz_jut}}
          </div>
        </div>
      </div>
      <div class="col s12 m4">
        <div class="card blue white-text">  
          <div class="card-content valign-wrapper">
            <div class="card-text">
                <h1>Ilość klientów zapisanych</h1>
            </div>
           <i class="container large material-icons">today</i>
          </div>
          <div class="card-action">
            {{licz_zap}}
          </div>
        </div>
      </div>
    </div>
  </div>
  <ul class="collection with-header">
           <li class="collection-header"><h4>Wizyty na dzień: {{teraz}}</h4></li>
            <li v-for="kli in klient" v-bind:key="kli.id" class="collection-item">
                <div class="chip orange white-text">{{kli.klient_id}} - {{kli.czas_uslugi}} </div>
                 {{kli.imie_nazwisko}}     
             </li>
        </ul>
</div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'wizyty',
    data (){
        return{
           klient: [],
          klient2: [],
          klient3: [],
        }
    },
 
    created () {
        db.collection('klient').where('data_wizyty','>=',this.teraz).where('data_wizyty','<',this.teraz+1).orderBy('data_wizyty').get().then
        (querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'klient_id': doc.data().klient_id,
                    'imie_nazwisko': doc.data().imie_nazwisko,
                    'data_wizyty': doc.data().data_wizyty,
                    'usluga': doc.data().usluga,
                    'czas_uslugi': doc.data().czas_uslugi,
                    'id_k': doc.data().id_k
                }
                this.klient.push(data)
            })
        }),
        db.collection('klient').where('data_wizyty','>=',this.teraz2).where('data_wizyty','<',this.teraz2+1).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'klient_id': doc.data().klient_id,
                    'imie_nazwisko': doc.data().imie_nazwisko,
                    'data_wizyty': doc.data().data_wizyty,
                    'usluga': doc.data().usluga,
                    'czas_uslugi': doc.data().czas_uslugi,
                    'id_k': doc.data().id_k
                }
                this.klient2.push(data)
            });
        })
       db.collection('klient').where('data_wizyty','>=',this.teraz).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'klient_id': doc.data().klient_id,
                    'imie_nazwisko': doc.data().imie_nazwisko,
                    'data_wizyty': doc.data().data_wizyty,
                    'usluga': doc.data().usluga,
                    'czas_uslugi': doc.data().czas_uslugi,
                    'id_k': doc.data().id_k
                }
                this.klient3.push(data)
            });
        }) 
    },
    computed:{
        teraz: function(){
            const toTwoDigits = num => num < 10 ? '0' + num : num;
            let today = new Date();
            let year = today.getFullYear();
            let month = toTwoDigits(today.getMonth() + 1);
            let day = toTwoDigits(today.getDate());
            return `${year}-${month}-${day}`;
        },
        teraz2: function(){
            const toTwoDigits = num => num < 10 ? '0' + num : num;
            let today = new Date();
            let year = today.getFullYear();
            let month = toTwoDigits(today.getMonth() + 1);
            let day = toTwoDigits(today.getDate() + 1);
            return `${year}-${month}-${day}`;
        },

        licz: function(){
            return Object.keys(this.klient).length
        },
        licz_jut: function(){
            return Object.keys(this.klient2).length
        },
        licz_zap: function(){
            return Object.keys(this.klient3).length
        }
  }
  
}
</script>
<style>

      .card {
        box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.64);
      }

      .container-fluid {
        padding: 1rem 1,5rem;
        margin: auto;
      }
         .card-text {
        width: calc(100% - 10px);
      }

      .card .card-action {
        border-top: none;
        font-size: 60px;  
        text-align: center
      }
      
      
     h1 {
        font-size: 20px;
        margin-top: 0;
        text-align: center
      }

    </style>


