import Vue from 'vue';
import Router from 'vue-router';
import wizyty from '@/components/wizyty';
import NowyKlient from '@/components/NowyKlient';
import KlientWidok from '@/components/KlientWidok';
import EdycjaKlient from '@/components/EdycjaKlient';
import dashboard from '@/components/dashboard';
import logowanie from '@/components/Logowanie';
import rejestracja from '@/components/Rejestracja';
import firebase from 'firebase';

Vue.use(Router);

let router = new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: dashboard,
      meta:{
        requiresAuth: true
      }
    },

    {
      path: '/logowanie',
      name:'logowanie',
      component: logowanie,
      meta: {
        requiresGuest: true
      }
    },
    {
      path: '/rejestracja',
      name:'rejestracja',
      component: rejestracja,
      meta:{
        requiresGuest: true
      }
    },
    {
      path: '/wizyty',
      name: 'wizyty',
      component: wizyty,
      meta:{
        requiresAuth: true
      }
    },
    {
      path: '/nowy',
      name: 'nowy-klient',
      component: NowyKlient,
      meta:{
        requiresAuth: true
      }
    },
    {
      path: '/:id_k',
      name: 'widok-klient',
      component: KlientWidok,
      meta:{
        requiresAuth: true
      }
    },
    {
      path: '/edycja/:id_k',
      name: 'edycja-klient',
      component: EdycjaKlient,
      meta:{
        requiresAuth: true
      }
    }
  ]
});

router.beforeEach((to, from, next) => {
  // Check for requiresAuth guard
  if (to.matched.some(record => record.meta.requiresAuth)) {
    // Check if NO logged user
    if (!firebase.auth().currentUser) {
      // Go to login
      next({
        path: '/logowanie',
        query: {
          redirect: to.fullPath
        }
      });
    } else {
      // Proceed to route
      next();
    }
  } else if (to.matched.some(record => record.meta.requiresGuest)) {
    // Check if NO logged user
    if (firebase.auth().currentUser) {
      // Go to login
      next({
        path: '/',
        query: {
          redirect: to.fullPath
        }
      });
    } else {
      // Proceed to route
      next();
    }
  } else {
    // Proceed to route
    next();
  }
});

export default router;


