<<template>
    <div id="nowy-klient">
        <h3>nowy klient</h3>
        <div class="row">
            <form @submit.prevent="saveKli" class="col s12">
                <div class="row">
                   <div class="input-field col s12"> 
                     <input type="text" v-model="imie_nazwisko" required>
                     <label>Imie i Nazwisko</label>  
                   </div>
                </div>
                <div class="row">
                   <div class="input-field col s12"> 
                     <input type="date" v-model="data_wizyty" required>
                     <label>data wizyty</label>  
                   </div>
                </div>
                                <div class="row">
                   <div class="input-field col s12"> 
                     <input type="time" v-model="klient_id" required>
                     <label>Godzina od</label>  
                   </div>
                    <div class="input-field col s12"> 
                     <input type="time" v-model="czas_uslugi" required>
                     <label>Godzina do</label>  
                   </div>
                </div>
                <div class="row">
                   <div class="input-field col s12"> 
                     <input type="text" v-model="usluga" required>
                     <label>Rodzaj usługi</label>  
                   </div>
                </div>
                <div class="row">

                </div>
                <button type="submit" class="btn">Dodaj</button>
                <router-link to="/wizyty" class="btn grey">Anuluj</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
import { error } from 'util';
export default {
    name: 'nowy-klient',
    data (){
        return{
            klient_id: null,
           imie_nazwisko: null,
           data_wizyty: null,
           usluga: null,
           czas_uslugi: null,
           id_k: null
        }
    },
    methods: {
        saveKli (){
         db.collection('klient').add({
               klient_id: this.klient_id,
               imie_nazwisko: this.imie_nazwisko,
               data_wizyty: this.data_wizyty,
               usluga: this.usluga,
               czas_uslugi: this.czas_uslugi,
               id_k: this.data_wizyty + this.klient_id+ this.imie_nazwisko
         })  
         .then(docRef => this.$router.push('/wizyty'))
         .catch(error => console.log(err))
        }
    }
}
</script>


