<<template>
    <div id="widok-klient">
        
       <ul class="collection with-header">
           <li class="collection-header"><h4>{{imie_nazwisko}}</h4></li>
           <li class="collection-item">data wizyty: {{data_wizyty}}</li>
           <li class="collection-item">godzina od: {{klient_id}}</li>
            <li class="collection-item">godzina do: {{czas_uslugi}}</li>
           <li class="collection-item">usługa: {{usluga}}</li>

        </ul>
        <router-link to="/wizyty" class="btn blue">wróć</router-link>
        <button @click="deleteKli" class="btn red">Usuń</button>

        <div class="fixed-action-btn">
           <router-link v-bind:to="{name: 'edycja-klient', params:{id_k: id_k}}"
            class="btn-floating btn-large blue">
           <i class="fa fa-pencil"></i>
           </router-link>
       </div> 
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'widok-klient',
    data (){
        return{
           klient_id: null,
           imie_nazwisko: null,
           data_wizyty: null,
           usluga: null,
           czas_uslugi: null,
           id_k: null
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('klient').where('id_k','==', to.params.id_k).get().then(
            querySnapshot => {
            querySnapshot.forEach(doc =>{
                next(vm => {
                    vm.klient_id = doc.data().klient_id
                    vm.imie_nazwisko = doc.data().imie_nazwisko
                    vm.data_wizyty = doc.data().data_wizyty
                    vm.usluga = doc.data().usluga
                    vm.czas_uslugi = doc.data().czas_uslugi
                    vm.id_k = doc.data().id_k
                })
             })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('klient').where('id_k','==',this.$route.params.id_k).get().then
            (querySnapshot => {
                querySnapshot.forEach(doc =>{
                    this.klient_id = doc.data().klient_id
                    this.imie_nazwisko = doc.data().imie_nazwisko
                    this.data_wizyty=doc.data().data_wizyty
                    this.usluga= doc.data().usluga
                    this.czas_uslugi= doc.data().czas_uslugi
                    this.id_k= doc.data().id_k
                })
            })
        },
        deleteKli (){
            if(confirm('czy na pewno usnąć?')) {
                 db.collection('klient').where('id_k','==',this.$route.params.id_k).get().then
                (querySnapshot => {
                    querySnapshot.forEach(doc =>{
                        doc.ref.delete()
                        this.$router.push('/wizyty')
                    })
                })
            }
        }
    }
}
</script>


